#drum-machine {
  display: grid;
  background-color: aqua;
  width: 60%;
  grid-gap: 10px;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  grid-template-areas:
    "tittle tittle tittle tittle"
    "buttonQ buttonW buttonE display"
    "buttonA buttonS buttonD display"
    "buttonZ buttonX buttonC display";
}

#title {
  text-align: center;
  grid-area: tittle;
}
#display {
  grid-area: display;
  text-align: center;
}
